@import url(https://fonts.googleapis.com/css?family=Poppins:400,500,600,700,800,900&display=swap);
@import url(https://fonts.googleapis.com/css?family=Montserrat:500,600,700,800,900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*   font-family: 'Poppins', sans-serif; */

body{
  font-family: 'Montserrat', sans-serif !important;
  font-size: 14px  !important;
}




.header-wrap{
    background-color: #101010;
    border-bottom:1px solid #000;
    padding:12px 0;
}

.header{
    font-size: 20px;
    font-weight: 700;
}

.menu{
    margin-bottom: 0;
    padding: 6px 0;
}

.menu li{ 
    list-style: none;
    display: inline-block;
}

.menu li a{
    color: #fff;
    padding: 10px 15px ;
    font-size: 17px;
    font-weight: 600;
}
.menu li a:hover{
    text-decoration: none;
    background:  -webkit-linear-gradient(145deg,#f81f01,#ee076e);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.left{
    text-align: left;
}

.right{
    text-align: right;
}

@media screen and (max-width:767px){
    .right,.left{
        text-align:  center !important;
    }
}
.footer-wrap{
    border-top:1px solid #000;
    background-color: #101010;
    padding: 15px 0;

}

.footer-wrap p {
    font-size: 14px;
    font-weight: 600;
    margin-bottom: 0;
    background:  -webkit-linear-gradient(145deg,#f81f01,#ee076e);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}
.card-wrapper .card{
    background: none;
}
.cards-wrap{
    background: #f8f9fe;
    margin-bottom: 20px;
    padding-bottom: 20px;
}

.card-wrapper{
    margin-top: 20px;
    margin-bottom: 20px;
    color: #fff;
}
.card-wrapper .col{
    border : none;
    
}
.card-wrapper .col .card-body{
    border-radius: 5px;
    background: linear-gradient(90deg, rgb(8, 174, 234) 0%, rgb(42, 245, 152) 100%);
    padding-top:20px;
    padding-bottom: 20px;
}

.card-wrapper .col .card-body p{
    font-size: 15px;
    font-weight: 700
}

.card-wrapper .col h2 {
    font-family: 'Montserrat', sans-serif;
    font-size: 28px;
    font-weight: 700;
    text-transform: capitalize;
}

.card-wrapper .col h3 span{
    font-family: 'Montserrat', sans-serif;
    font-size: 24px;
    font-weight: 700;
}

.card-wrapper .col:nth-child(2) .card-body{
    background-image: linear-gradient(90deg, rgb(33, 212, 253) 0%, rgb(183, 33, 255) 100%);
}

.card-wrapper .col:nth-child(4) .card-body{
    background-image: linear-gradient(90deg, rgb(255, 154, 139) 0%, rgb(255, 106, 136) 55%, rgb(255, 153, 172) 100%)
}

.total-case{
    background:  -webkit-linear-gradient(145deg,#f81f01,#ee076e);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-family: 'Montserrat', sans-serif;
    font-size: 16px;
    font-weight: 700;
}

/*  Responsive */
@media screen and (max-width : 767px){
    .card-wrapper .col{
       margin-bottom: 20px;
        
    }
}
.chart-container{
    display: flex;
    margin-bottom: 50px;
}
.country-picker{
    background: #f8f9fe;
    padding-top: 20px;
}

.country-picker form{
    margin-top: 20px;
}
.country-picker label{
    font-family: 'Montserrat', sans-serif;
    font-size: 24px;
    font-weight: 700;
    text-transform: uppercase;
}

.content-wrap{
    background:#101010;
    color: #c6c9d8;
    padding: 60px 0;
 }

.content h4{
    font-size: 25px;
    text-transform: uppercase;
    font-family: 'Montserrat', sans-serif;
    font-weight: 600;
    margin-bottom: 15px;
}

.content h4 span{
    display: inline-block;
    margin-right: 8px;
    background:  -webkit-linear-gradient(145deg,#f81f01,#ee076e);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.content p{
    font-size: 16px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 500;
}

.india-fig-wrap{
    background: #f8f9fe;
    padding:50px 0 60px 0;
}

.header-wrap h1{
    background:  -webkit-linear-gradient(145deg,#f81f01,#ee076e);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    font-size: 36px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    margin-bottom: 0;
  }


.india-figure{
    display: grid;
    grid-template-columns: auto auto ;
}

.indFlag{
    height: 100px;
    width: 100px;
}
.india{
    background:  -webkit-linear-gradient(145deg,#f81f01,#ee076e, #f81f01);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.india-fig-left {
    align-items:  center;
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
}
.india-fig-left h3{
    color: #1f1f25;
    font-size: 30px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
    margin-bottom: 0;
    padding-left: 20px;
    display: table-cell;
    vertical-align: middle;
}

.india-fig-right div{
    padding: 10px ;
}

.india-fig-right h4{
    color: #1f1f25;
    font-size: 36px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
}

.india-fig-right  span{
    background:  -webkit-linear-gradient(145deg,#f81f01,#ee076e);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    display: block;
    font-size: 28px;
    font-family: 'Montserrat', sans-serif;
    font-weight: 700;
}

.chart-wpr{
    width: 100%;
    height: 100%;
}


@media screen and (max-width :767px ){
    .india-figure{
        grid-template-columns: auto ;
    }
}
/* Prevention Css */

.prevention{
    background:#101010;
    color: #c6c9d8;
    padding: 80px 0;
 }
 
 .advice{
     font-size: 85px;
     text-transform: uppercase;
     font-family: 'Montserrat', sans-serif;
     font-weight: 900;
     line-height: 90px;
     padding-bottom: 50px;
 }
 .theme-gradient {
     background:  -webkit-linear-gradient(145deg,#f81f01,#ee076e);
     background-clip: text;
     -webkit-background-clip: text;
     -webkit-text-fill-color: transparent;
 }
 
 .content h4{
     font-size: 25px;
     text-transform: uppercase;
     font-family: 'Montserrat', sans-serif;
     font-weight: 600;
     margin-bottom: 15px;
 }
 
 .content h4 span{
     display: inline-block;
     margin-right: 8px;
     background:  -webkit-linear-gradient(145deg,#f81f01,#ee076e);
     background-clip: text;
     -webkit-background-clip: text;
     -webkit-text-fill-color: transparent;
 }
 
 .content p{
     font-size: 16px;
     font-family: 'Montserrat', sans-serif;
     font-weight: 500;
 }


 @media screen and (max-width :767px ){
    
    .advice{
        font-size: 70px;
        line-height: 75px;
    }
}
.backToTop{border-radius:50%;cursor:pointer;position:fixed;bottom:100px;right:30px;height:60px;width:60px;background:#f61e1c;padding:17px 10px;text-transform:uppercase;font-weight:900;color:#fff;box-shadow:inset 2px 2px 15px #484848;text-shadow:2px 1px 1px #444;font-size:9px;text-align:center}
.table-wrap{
    background: #f8f9fe;
    padding: 30px 0;
}

.table-wrap table{
    margin-bottom: 0;
}

.table-wrap table th{
    font-weight: 700;
}

.table-wrap table td{
    font-weight: 600;
}

.text-center{
    text-align: center;
}

.green{
    color: #02d05d;
    text-shadow: 2px 8px 6px rgba(0,0,0,0.2),
                 0px -5px 35px rgba(255,255,255,0.3);
}

.red {
    color: #f30404;
    text-shadow: 2px 8px 6px rgba(0,0,0,0.2),
                 0px -5px 35px rgba(255,255,255,0.3);
}

.yellow{
    color: #f1c00b;
    text-shadow: 2px 8px 6px rgba(0,0,0,0.2),
                 0px -5px 35px rgba(255,255,255,0.3);
}

.skyblue{
    color: #0ce0e0;
    text-shadow: 2px 8px 6px rgba(0,0,0,0.2),
                 0px -5px 35px rgba(255,255,255,0.3);
}

.country-image{
    display: inline-block;
    margin-right: 5px;
}

.serious{
    color: rgba(25,184,255);
    text-shadow: 2px 8px 6px rgba(0,0,0,0.2),
                 0px -5px 35px rgba(255,255,255,0.3);
}

.green-bg{
    background: green;
}

.get-btn.btn-outline-warning{
    font-size: 10px;
}

.get-btn{
    border : 1px solid #ffc107;
    border-radius: 2px;
    color: #ffc107;
    display: inline-block;
    font-size: 11px;
    font-weight: 600;
    padding: 6px 10px;
    
    
    text-decoration: none;
    
}

.get-btn:hover, .get-btn.active{
    background:#ffc107 ;
    color: #212529;
    text-decoration: none;
}



.loader-back{
    min-height: calc(100vh - 120px);
    display: flex;
    justify-content: center;
    align-items:center;
}
.country-details{background:#f8f9fe;position:relative}.country-details::before{background:rgba(255,255,255,.96);content:"";position:absolute;top:0;left:0;width:100%;height:100%}.country-details .country-header{position:relative;padding:15px 0}.country-details .country-header h2{color:#1f1f25;font-size:34px;font-weight:700}.country-details .country-header img{height:64px;width:64px}.country-details .con-details{display:grid;grid-template-columns:auto auto auto auto;grid-gap:10px;margin-bottom:40px}.country-details .con-details div{text-align:center;padding:20px 0;font-size:30px}.country-details .con-details div h2{color:#1f1f25;font-size:30px;font-weight:700}.country-details .con-details div h2 span{background:-webkit-linear-gradient(145deg, #f81f01, #ee076e);background-clip:text;-webkit-background-clip:text;-webkit-text-fill-color:transparent;display:block;font-size:20px;font-family:"Montserrat",sans-serif;font-weight:700}.country-details button{margin:20px 0}.country-details button a{color:#212529;text-decoration:none}@media screen and (max-width: 767px){.country-details .con-details{grid-template-columns:auto}.country-details .con-details div{padding:5px 0}}
.not-found{background:#f8f9fe;height:calc(100vh - 120px)}.not-found .wrapper{display:flex;justify-content:center;align-items:center}.not-found .wrapper h1{font-size:40px;font-weight:800;padding-top:60px;text-transform:uppercase}.not-found .wrapper button{margin-top:30px}.not-found .wrapper button a{color:#212529;text-decoration:none}
